import BaseContent from '../../components/BaseContent/BaseContent'

export default {
  name: 'keep-alive-doc',
  components: { BaseContent },
  data () {
    return {
      content: ''
    }
  },
  methods: {
    getMsg () {
      const query = {
        url: './data/keepAliveData.md',
        method: 'get',
        responseType: 'text'
      }
      this.$fetchData(query).then(res => {
        this.content = res.data
      }).catch(error => {
        console.log(error)
      })
    }
  },
  mounted () {
    this.getMsg()
  }
}
